import { Injectable } from '@angular/core';
import { CanLoad, CanActivate, RouterStateSnapshot, ActivatedRouteSnapshot } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { Router, Route } from '@angular/router';
import 'rxjs/add/operator/map';
import { StorageService } from '../Services/todo.storage.service';

@Injectable()
export class AuthGaurd implements CanLoad, CanActivate {

    constructor(private session: StorageService, private router: Router) {}

    canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<boolean> | Promise<boolean> | boolean {
        if (!this.session.retrive('token', false)) {
            this.router.navigate(['/landing/login']);
            return false;
        } else {
            return true;
        }
    }

    canLoad(route: Route): Observable<boolean> | Promise<boolean> | boolean {
        if (!this.session.retrive('token', false)) {
            this.router.navigate(['/landing/login']);
            return false;
        } else {
            return true;
        }
    }
}



