import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';

import { UtilsService, FormErrors } from '../../Shared/Services/todo.utils.service';
import { LoginService } from './Services/todo.login.service';
import { StorageService } from '../../Shared/Services/todo.storage.service';

import { UserLogin } from './Models/todo.login.models';



@Component({
    selector: 'todo-login',
    templateUrl: 'todo.login.component.html',
    styleUrls: ['todo.login.component.scss'],
    providers: [LoginService]
})
export class LoginComponent implements OnInit {
    loginForm: FormGroup;
    errors: FormErrors;

    // tslint:disable-next-line:max-line-length
    constructor(private fb: FormBuilder, private utilService: UtilsService, private loginService: LoginService, private router: Router, private session: StorageService) {}

    ngOnInit() {
        this.loginForm = this.fb.group({
            'email': [null, [Validators.required, Validators.email]],
            'password': [null, [Validators.required]]
        });

        this.errors = {
            valid: false,
            message: null,
            controls: {
                email : {
                    required: false,
                    email: false
                },
                password: {
                    required: false
                }
            }
        };
    }

    onLogin() {
        if (this.utilService.validate(this.errors, this.loginForm)) {
            const user = new UserLogin(this.loginForm.value.email, this.loginForm.value.password);
            this.loginService.LoginUser(user)
            // .map(response => response.json())
            .subscribe((response) => {
                const payload = response.json();
                const headers = response.headers;
                if (payload.success) {
                    this.session.store('token', headers.get('x-auth'), false);
                    this.session.store('sync', payload.cloudsync, false);
                    this.router.navigate(['home']);
                }
            }, (error) => {
                const payload = error.json();
                if (!payload.success) {
                    this.errors.valid = false;
                    this.errors.message = payload.err_msg;
                }
            });
        }
    }
}



